body{
  text-align:center;
  padding: 15px;
	min-width: 800px;
	min-height: 400px
}

.ballHolder {
  margin-top: 100px;
}

.pokeBall{
  width:200px;
  height:200px;
  background-color:#302d2d;
  border-radius:30px;
  position:relative;
  font-size:200px;
  line-height:200px;
  display:inline-block;
}
.pokeBall i:before{
  content:"";
  position:absolute;
  top:50%;
  left:50%;
  height:44px;
  width:44px;
  border-radius:44px;
  background-color:#f6f7f8;
  z-index:2;
  margin:-41px 0 0 -41px;
  border:18px solid #302d2d;
}
.pokeBall:before{
  content:"";
  width:200px;
  height:75px;
  background-color:#ef4144;
  display:block;
  border-radius:30px 30px 0 0;
  position:absolute;
  top:0;
  left:0;
}

.pokeBall:after{
  content:"";
  width:200px;
  height:96px;
  background-color:#f6f7f8;
  display:block;
  border-radius:0 0 30px 30px;
  position:absolute;
  bottom:0;
  left:0;
  -webkit-box-shadow: inset 0px -20px 0px 0px #e2e3e4;
  box-shadow: inset 0px -20px 0px 0px #e2e3e4;
}

footer {
  margin-top: 115px;
}